import Vue from 'vue'
import vuelidate from 'vuelidate'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import dateFilter from '@/filters/date.filter';
import currencyFilter from './filters/currency.filter'
import localizeFilter from './filters/localize.filter'
import VueMeta from 'vue-meta'
import tooltipDirective from './directives/tooltip.directive'
import messagePlugin from '@/utils/message.plugin'
import titlePlugin from '@/utils/title.plugin'
import 'materialize-css/dist/css/materialize.min.css';
import 'materialize-css/dist/js/materialize.min.js';
import Paginate from 'vuejs-paginate'
import './assets/index.css';
import firebase from 'firebase/compat/app'
import 'firebase/compat/auth'
import 'firebase/compat/database'
import Loader from './components/app/Loader.vue'

Vue.config.productionTip = false

Vue.use(vuelidate)
Vue.use(messagePlugin)
Vue.use(titlePlugin)
Vue.use(VueMeta)
Vue.filter('date', dateFilter)
Vue.filter('currency', currencyFilter)
Vue.filter('localize', localizeFilter)
Vue.directive('tooltip', tooltipDirective)
Vue.component("Loader", Loader)
Vue.component('paginate', Paginate)

firebase.initializeApp({
  apiKey: "AIzaSyCtV_frBMWQE7JiMdqdJd1Nkdjp_GdTrho",
  authDomain: "vue-crm-21e2d.firebaseapp.com",
  databaseURL: "https://vue-crm-21e2d-default-rtdb.firebaseio.com",
  projectId: "vue-crm-21e2d",
  storageBucket: "vue-crm-21e2d.appspot.com",
  messagingSenderId: "313041217238",
  appId: "1:313041217238:web:534e90a794115419d04b77"
})

let app;
firebase.auth().onAuthStateChanged(() => {
  if(!app){
    app = new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount('#app')
  }
})
